import React, {Component, PropTypes} from 'react'
import {Button, Table, Label} from 'react-bootstrap'
import moment from 'moment'

import {SYSTEM_CONTRACT_NAME, ORGANIZATION_CONTRACT_NAME, APPLICANT_CONTRACT_NAME, TOPIC_NAME} from '../configure'
import {unlockAccount, watchTransaction} from '../utils'

import Alert from '../Alert.jsx'

export default class Permissions extends Component {

  static propTypes = {
    web3: PropTypes.object.isRequired,
    account: PropTypes.string.isRequired,
    password: PropTypes.string.isRequired,
    lockedTime: PropTypes.number.isRequired,
    balance: PropTypes.object.isRequired,
    abi: PropTypes.string.isRequired,
    agent: PropTypes.string.isRequired,
    profileKey: PropTypes.string.isRequired,
    onPublishResponse: PropTypes.func.isRequired,
    onRemovePublishContact: PropTypes.func.isRequired,
    onAutoUnlocked: PropTypes.func.isRequired
  }

  state = {
    permissions: [],
    profile: null,
    resumes: [],
    message: null
  }

  loadPermissions(props) {
    const abi = JSON.parse(props.abi)

    const eth = this.props.web3.eth
    const instance = eth.contract(abi[ORGANIZATION_CONTRACT_NAME]).at(props.agent)

    const size = instance.sizePermissions({from: props.account}).toNumber()
    const permissions = []
    for (let i = 0; i < size; i++) {
      const permission = instance.getPermission(i, {from: props.account})
      const from = permission[1].toNumber()
      const to = permission[2].toNumber()
      const responseTime = permission[3].toNumber()

      let profile = null
      let identity = ''
      if (responseTime > 0) {
        profile = localStorage.getItem(this.props.profileKey + permission[0] + from + to)
        if (profile) {
          profile = JSON.parse(profile)
        } else {
          identity = instance.getPublishContact(permission[0], from, to, {from: props.account})
        }
      }

      permissions.push({
        applicantAgent: permission[0], from: permission[1].toNumber(), to: to, responseTime: responseTime,
        profile: profile, identity: identity
      })
    }
    return permissions
  }

  removeContact(props, permission) {
    try {
      unlockAccount(this.props.web3.personal, props.lockedTime, props.account, props.password, (result, unlockTime) => {
        if (result) {
          if (unlockTime) {
            this.props.onAutoUnlocked(unlockTime)
          }

          const abi = JSON.parse(props.abi)

          const eth = this.props.web3.eth
          const instance = eth.contract(abi[ORGANIZATION_CONTRACT_NAME]).at(props.agent)

          const gas = instance.removePublishContact.estimateGas(permission.applicantAgent, permission.from,
            permission.to, {from: props.account})
          console.log('removePublishContact gas: ' + gas)

          const transactionHash = instance.removePublishContact(permission.applicantAgent, permission.from,
            permission.to, {from: props.account, gas: gas})
          console.log('removePublishContact transactionHash: ' + transactionHash)

          watchTransaction(eth, transactionHash, (err, receipt) => {
            if (!err) {
              console.log(receipt)
              this.props.onRemovePublishContact(permission)
            } else {
              console.error(err)
            }
          })
        } else {
          console.error(err)
        }
      })
    } catch (err) {
      console.error(err)
    }
  }

  watchTopic(props, permission) {
    console.log('watch topic')

    const web3 = this.props.web3
    const shh = web3.shh
    const filter = shh.filter({topics: [web3.fromAscii(TOPIC_NAME)], to: permission.identity})
    filter.watch((err, result) => {
      filter.stopWatching()
      if (!err) {
        const json = web3.toAscii(result.payload)
        const profile = JSON.parse(json)
        console.log('recv {profile: %s}', json)

        localStorage.setItem(this.props.profileKey + permission.applicantAgent + permission.from + permission.to, json)

        this.removeContact(props, permission)

        const permissions = this.loadPermissions(props)
        if (permissions) {
          this.setState({permissions: permissions})
        }
      } else {
        console.error(err)
      }
    })
  }

  publishResponse(props, permission) {
    try {
      unlockAccount(this.props.web3.personal, props.lockedTime, props.account, props.password, (result, unlockTime) => {
        if (result) {
          if (unlockTime) {
            this.props.onAutoUnlocked(unlockTime)
          }

          const abi = JSON.parse(props.abi)

          const web3 = this.props.web3
          const eth = web3.eth
          const instance = eth.contract(abi[ORGANIZATION_CONTRACT_NAME]).at(props.agent)

          const shh = web3.shh
          const identity = shh.newIdentity()
          console.log('publishResponse {identity: %s}', identity)

          const gas1 = instance.publishResponse.estimateGas(permission.applicantAgent, permission.from,
            permission.to, identity, {from: props.account})

          const instance2 = eth.contract(abi[APPLICANT_CONTRACT_NAME]).at(permission.applicantAgent)
          const gas2 = instance2.publishResponse.estimateGas(this.props.account, permission.from, permission.to,
            identity, {from: props.agent})

          const gas = gas1 + gas2
          console.log('publishResponse {gas: %d}', gas)

          const transactionHash = instance.publishResponse(permission.applicantAgent, permission.from,
            permission.to, identity, {from: props.account, gas: gas})
          console.log('publishResponse {transactionHash: %s}', transactionHash)

          watchTransaction(eth, transactionHash, (err, receipt) => {
            if (!err) {
              console.log(receipt)
              this.props.onPublishResponse(permission)
            } else {
              console.error(err)
            }
          })

          permission.identity = identity
          this.watchTopic(props, permission)
        } else {
          console.error(err)
        }
      })
    } catch (err) {
      console.error(err)
    }
  }

  watchNotify(props) {
    console.log('watch PublishNotify')

    const abi = JSON.parse(props.abi)

    const eth = this.props.web3.eth
    const instance = eth.contract(abi[ORGANIZATION_CONTRACT_NAME]).at(props.agent)

    instance.PublishNotify().watch((err, event) => {
      if (!err) {
        console.log('PublishNotify event {sender: %s}', event.args.sender)

        const permissions = this.loadPermissions(props)
        if (permissions) {
          this.setState({permissions: permissions})

          for (let i in permissions) {
            if (permissions[i].responseTime == 0) {
              this.publishResponse(props, permissions[i])
            } else if (!permissions[i].profile) {
              this.watchTopic(props, permissions[i])
            }
          }
        }
      } else {
        console.error(err)
      }
    })
  }

  componentWillReceiveProps(nextProps) {
    if (!this.loaded && nextProps.account.length > 0 && nextProps.abi.length > 0 && nextProps.agent.length > 0) {
      const permissions = this.loadPermissions(nextProps)
      if (permissions) {
        this.setState({permissions: permissions})
      }

      this.loaded = true
    }

    if (!this.preprocessed && nextProps.account.length > 0 && nextProps.password.length > 0
      && nextProps.balance.toNumber() > 0 && nextProps.abi.length > 0 && nextProps.agent.length > 0) {
      for (let i in this.state.permissions) {
        const permission = this.state.permissions[i]
        if (permission.responseTime == 0) {
          this.publishResponse(nextProps, permission)
        } else if (permission.approveTime == 0) {
          if (!permission.profile) {
            this.watchTopic(nextProps, permission)
          }
        }
      }

      this.watchNotify(nextProps)

      this.preprocessed = true
    }
  }

  accessResumesClick(index) {
    const permission = this.state.permissions[index]

    const now = moment().unix()

    if (permission.from > now || permission.to < now) {
      this.setState({message: ''})
      return
    }

    this.state.accessing[index] = true
    this.setState({accessing: this.state.accessing})

    const abi = JSON.parse(this.props.abi)

    const web3 = this.props.web3
    const eth = web3.eth
    const instance = eth.contract(abi[APPLICANT_CONTRACT_NAME]).at(permission.applicantAgent)

    try {
      const size = instance.sizeResumes({from: this.props.account}).toNumber()
      const resumes = []
      for (let i = 0; i < size; i++) {
        const resume = instance.getResume(i, {from: this.props.account})
        if (resume[6]) {
          resumes.push({
            orgAgent: resume[0], name: resume[1], from: resume[2].toNumber(), to: resume[3].toNumber(),
            createTime: resume[4].toNumber(), acceptTime: resume[5].toNumber(), approveResult: resume[6],
            approveTime: resume[7].toNumber()
          })
        }
      }

      this.state.accessing[index] = false
      this.setState({profile: permission.profile, resumes: resumes, accessing: this.state.accessing})
    } catch (err) {
      console.error(err)
      this.state.accessing[index] = false
      this.setState({accessing: this.state.accessing})
    }
  }

  formatMonth(time) {
    if (time > 0) {
      return moment.unix(time).format('YYYY/MM')
    }
    return ''
  }

  formatDay(time) {
    if (time > 0) {
      return moment.unix(time).format('YYYY/MM/DD')
    }
    return ''
  }

  formatTime(time) {
    if (time > 0) {
      return moment.unix(time).format('YYYY/MM/DD HH:mm:ss')
    }
    return ''
  }

  render() {
    const now = moment().unix()

    const rows = []
    for (let i in this.state.permissions) {
      const permission = this.state.permissions[i]
      rows.push(<tr key={i}>
        <td>{(permission.profile) ?
          <div>Name: {permission.profile.name}<br/>Birthday: {permission.profile.birthday}
          </div> : permission.applicantAgent}</td>
        <td>{this.formatDay(permission.from)}</td>
        <td>{this.formatDay(permission.to)}</td>
        <td>{(permission.responseTime > 0) ?
          <Button bsStyle="info" bsSize="small" onClick={this.accessResumesClick.bind(this, i)}
                  disabled={permission.from > now || permission.to < now || !permission.profile}>Access</Button>
          : <Label bsStyle="default">Waiting</Label>}</td>
      </tr>)
    }

    let resumes
    if (this.state.profile) {
      const resumesRows = []
      for (let i in this.state.resumes) {
        const resume = this.state.resumes[i]
        resumesRows.push(<tr key={i}>
          <td>{resume.name}</td>
          <td>{this.formatMonth(resume.from)}</td>
          <td>{this.formatMonth(resume.to)}</td>
        </tr>)
      }

      resumes = <div>
        <hr/>
        <h5>{this.state.profile.name} resume</h5>
        <Table striped bordered condensed hover>
          <thead>
          <tr>
            <th>Company / School</th>
            <th>From</th>
            <th>To</th>
          </tr>
          </thead>
          <tbody>
          {resumesRows}
          </tbody>
        </Table>
      </div>
    }

    return <div>
      <Table striped bordered condensed hover>
        <thead>
        <tr>
          <th>Job applicant</th>
          <th>From</th>
          <th>End</th>
          <th></th>
        </tr>
        </thead>
        <tbody>
        {rows}
        </tbody>
      </Table>
      {resumes}
      <Alert message={this.state.message} onHide={() => this.setState({message: null})}/>
    </div>
  }
}
