import React, {Component, PropTypes} from 'react'
import {Button, Table}
  from 'react-bootstrap'
import moment from 'moment'

import {SYSTEM_CONTRACT_NAME} from '../configure'
import {unlockAccount, watchTransaction} from '../utils'

export default class Organizations extends Component {

  static propTypes = {
    web3: PropTypes.object.isRequired,
    account: PropTypes.string.isRequired,
    password: PropTypes.string.isRequired,
    lockedTime: PropTypes.number.isRequired,
    balance: PropTypes.object.isRequired,
    abi: PropTypes.string.isRequired,
    agent: PropTypes.string.isRequired,
    onStopped: PropTypes.func.isRequired,
    onAutoUnlocked: PropTypes.func.isRequired
  }

  state = {
    organizations: [],
    stopping: {}
  }

  loadOrganizations(props) {
    const abi = JSON.parse(props.abi)

    const eth = this.props.web3.eth
    const instance = eth.contract(abi[SYSTEM_CONTRACT_NAME]).at(props.agent)

    const size = instance.sizeOrganizations({from: props.account}).toNumber()
    const organizations = []
    for (let i = 0; i < size; i++) {
      const organization = instance.getOrganization(i, {from: props.account})
      if (organization.length == 5) {
        organizations.push({
          account: organization[0], name: organization[1], identity: organization[2], agent: organization[3],
          createTime: organization[4].toNumber()
        })
      } else {
        organizations.push({
          account: organization[0], name: organization[1], agent: organization[2],
          createTime: organization[3].toNumber()
        })
      }
    }
    return organizations
  }

  watchUpdate(props) {
    console.log('watch OrganizationsUpdate')

    const abi = JSON.parse(props.abi)

    const eth = this.props.web3.eth
    const instance = eth.contract(abi[SYSTEM_CONTRACT_NAME]).at(props.agent)

    instance.OrganizationsUpdate().watch((err, event) => {
      if (!err) {
        console.log('OrganizationsUpdate event {sender: %s}', event.args.sender)

        const organizations = this.loadOrganizations(props)
        if (organizations) {
          this.setState({organizations: organizations})
        }
      } else {
        console.error(err)
      }
    })
  }

  componentWillReceiveProps(nextProps) {
    if (!this.loaded && nextProps.account.length > 0 && nextProps.abi.length > 0 && nextProps.agent.length > 0) {
      const organizations = this.loadOrganizations(nextProps)
      if (organizations) {
        this.setState({organizations: organizations})
      }
      this.watchUpdate(nextProps)

      this.loaded = true
    }
  }

  stopAgentClick(agent) {
    this.state.stopping[agent] = true
    this.setState({stopping: this.state.stopping})

    try {
      unlockAccount(this.props.web3.personal, this.props.lockedTime, this.props.account, this.props.password,
        (result, unlockTime) => {
          if (result) {
            if (unlockTime) {
              this.props.onAutoUnlocked(unlockTime)
            }

            const abi = JSON.parse(this.props.abi)

            const eth = this.props.web3.eth
            const instance = eth.contract(abi[SYSTEM_CONTRACT_NAME]).at(this.props.agent)

            const gas = instance.removeOrganization.estimateGas(agent, {from: this.props.account})
            console.log('removeOrganization {gas: %d}', gas)

            const transactionHash = instance.removeOrganization(agent, {from: this.props.account, gas: gas})
            console.log('removeOrganization {transactionHash; %s}', transactionHash)

            watchTransaction(eth, transactionHash, (err, receipt) => {
              if (!err) {
                console.log(receipt)

                this.props.onStopped(agent)
                for (let i in this.state.organizations) {
                  if (this.state.organizations[i].agent == agent) {
                    this.state.organizations.splice(i, 1);
                    break;
                  }
                }
                delete this.state.stopping[agent]
                this.setState({stopping: this.state.stopping, organizations: this.state.organizations})
              } else {
                console.error(err)
                delete this.state.stopping[agent]
                this.setState({stopping: this.state.stopping})
              }
            })
          }
        })
    } catch (err) {
      console.error(err)
      delete this.state.stopping[agent]
      this.setState({stopping: this.state.stopping})
    }
  }

  formatTime(time) {
    if (time > 0) {
      return moment.unix(time).format('YYYY/MM/DD HH:mm:ss')
    }
    return ''
  }

  render() {
    const rows = []
    for (let i in this.state.organizations) {
      const organization = this.state.organizations[i]
      rows.push(<tr key={i}>
        <td style={{width: 200}}>
          <div style={{width: 200, overflowX: 'scroll'}}>{organization.agent}</div>
        </td>
        <td style={{width: 200}}>
          <div style={{width: 200, overflowX: 'scroll'}}>{organization.account}<br/>
            {organization.name}</div>
        </td>
        <td>{this.formatTime(organization.createTime)}</td>
        <td><Button bsStyle="danger" bsSize="small" onClick={this.stopAgentClick.bind(this, organization.agent)}
                    disabled={this.props.password.length == 0 || this.props.balance.toNumber() == 0
                    || this.state.stopping[organization.agent]}
        >{(this.state.stopping[organization.agent]) ? 'Stopping ...' : 'Stop agent'}</Button></td>
      </tr>)
    }

    return <Table striped bordered condensed hover>
      <thead>
      <tr>
        <th>Agent</th>
        <th>Account</th>
        <th>Create time</th>
        <th></th>
      </tr>
      </thead>
      <tbody>
      {rows}
      </tbody>
    </Table>
  }
}
