import React, {Component, PropTypes} from 'react'
import {FormGroup, ControlLabel, FormControl, Button, Table, Label} from 'react-bootstrap'
import moment from 'moment'

import {unlockAccount} from '../utils'

import Approvals2 from './Approvals2.jsx'

export default class Approvals3 extends Approvals2 {

  static propTypes = {
    web3: PropTypes.object.isRequired,
    device: PropTypes.string,
    account: PropTypes.string.isRequired,
    password: PropTypes.string.isRequired,
    lockedTime: PropTypes.number.isRequired,
    profile: PropTypes.shape({
      identity: React.PropTypes.string,
      name: React.PropTypes.string,
      createTime: React.PropTypes.number,
      updateTime: React.PropTypes.number
    }),
    abi: PropTypes.string.isRequired,
    topicName: PropTypes.string.isRequired,
    onAutoUnlocked: PropTypes.func.isRequired
  }

  postApprovalResponse(approval, callback) {
    console.log('post approval response {id: %s, result: %s}', approval.resume.id, approval.resume.approveResult)
    const payload = {
      id: approval.resume.id, approveResult: approval.resume.approveResult, approveTime: approval.resume.approveTime,
      signature: approval.resume.signature
    }
    this.postResponse(this.props.profile.identity, approval.profile.identity, payload, callback)
  }

  approveClick(index, approveResult) {
    this.state.approving[index] = true
    this.setState({approving: this.state.approving})

    const approval = this.state.approvals[index]

    const web3 = this.props.web3

    const hash = web3.sha3(approval.profile.identity + approval.resume.id)

    try {
      unlockAccount(this.props.web3.personal, this.props.lockedTime, this.props.account, this.props.password,
        (result, unlockTime) => {
          if (result) {
            if (unlockTime) {
              this.props.onAutoUnlocked(unlockTime)
            }

            approval.resume.approveResult = approveResult
            approval.resume.approveTime = moment().unix()
            approval.resume.signature = web3.eth.sign(this.props.account, hash)

            this.postApprovalResponse(approval, (err, result) => {
              if (!err) {
                delete this.state.approving[index]
                this.setState({approving: this.state.approving, approvals: this.state.approvals})
              } else {
                delete this.state.approving[index]
                this.setState({approving: this.state.approving})
              }
            })
          } else {
            console.error(err)
            delete this.state.approving[index]
            this.setState({approving: this.state.approving})
          }
        })
    } catch (err) {
      console.error(err)
      delete this.state.approving[approval.applicantAgent + approval.from]
      this.setState({approving: this.state.approving})
    }
  }

  render() {
    const rows = []
    for (let i in this.state.approvals) {
      const approval = this.state.approvals[i]

      rows.push(<tr key={i}>
        <td>Name: {approval.profile.name}<br/>Birthday: {approval.profile.birthday}</td>
        <td>{this.formatMonth(approval.resume.from)}</td>
        <td>{this.formatMonth(approval.resume.to)}</td>
        <td>{(approval.resume.approveTime > 0) ? (approval.resume.approveResult)
          ? <div><Label bsStyle="primary">Approved</Label>&nbsp;{this.formatTime(approval.resume.approveTime)}</div>
          : <div><Label bsStyle="danger">Disapproved</Label>&nbsp;{this.formatTime(approval.resume.approveTime)}</div>
          : (approval.resume.acceptTime > 0) ? <div>
          <Button bsStyle="info" bsSize="small" onClick={this.approveClick.bind(this, i, true)}
                  disabled={this.props.password.length == 0 || this.state.approving[i]}
                  style={{width: 100}}>Approve</Button>&nbsp;
          <Button bsStyle="danger" bsSize="small" onClick={this.approveClick.bind(this, i, false)}
                  disabled={this.props.password.length == 0 || this.state.approving[i]}>Disapprove</Button>
          <br/>&nbsp;<Label bsStyle="warning">Accepted</Label>&nbsp;{this.formatTime(approval.resume.acceptTime)}</div>
          : <Label bsStyle="default">Accepting</Label>}</td>
      </tr>)
    }

    return <Table striped bordered condensed hover>
      <thead>
      <tr>
        <th>Job applicant</th>
        <th>From</th>
        <th>To</th>
        <th></th>
      </tr>
      </thead>
      <tbody>
      {rows}
      </tbody>
    </Table>
  }
}
