import React, {Component, PropTypes} from 'react'
import {FormGroup, ControlLabel, FormControl, Button, Table, Label} from 'react-bootstrap'
import moment from 'moment'

import {SYSTEM_CONTRACT_NAME, ORGANIZATION_CONTRACT_NAME, ORGANIZATION_APPROVALS_KEY, ORGANIZATION_APPROVAL_RECV_HASHES,
  TOPIC_MSG_TTL} from '../configure'
import {unlockAccount, watchTransaction} from '../utils'

export default class Approvals2 extends Component {

  static propTypes = {
    web3: PropTypes.object.isRequired,
    device: PropTypes.string,
    account: PropTypes.string.isRequired,
    password: PropTypes.string.isRequired,
    lockedTime: PropTypes.number.isRequired,
    balance: PropTypes.object.isRequired,
    profile: PropTypes.shape({
      identity: React.PropTypes.string,
      name: React.PropTypes.string,
      createTime: React.PropTypes.number,
      updateTime: React.PropTypes.number
    }),
    abi: PropTypes.string.isRequired,
    agent: PropTypes.string.isRequired,
    topicName: PropTypes.string.isRequired,
    onApproved: PropTypes.func.isRequired,
    onAutoUnlocked: PropTypes.func.isRequired
  }

  static defaultProps = {
    device: '0'
  }

  state = {
    approvals: [],
    approving: {}
  }

  loadApprovals() {
    const approvals = []
    let size = localStorage.getItem(ORGANIZATION_APPROVALS_KEY + this.props.device + '.size')
    if (size) {
      size = Number(size)
      for (let i = 0; i < size; i++) {
        approvals.push(JSON.parse(localStorage.getItem(ORGANIZATION_APPROVALS_KEY + this.props.device + '.' + i)))
      }
    }
    return approvals
  }

  componentDidMount() {
    this.setState({approvals: this.loadApprovals()})
  }

  watchApprovalRequest(props) {
    console.log('watch approval request')

    let hashes = localStorage.getItem(ORGANIZATION_APPROVAL_RECV_HASHES + this.props.device)
    if (hashes) {
      this.hashes = {}
      const size = hashes.length / 66
      for (let i = 0; i < size; i++) {
        const hash = hashes.substring(i * 66, (i + 1) * 66)
        this.hashes[hash] = true
      }
    } else {
      hashes = ''
      this.hashes = {}
    }

    const web3 = this.props.web3
    const shh = web3.shh
    const filter = shh.filter({topics: [this.props.topicName], to: props.profile.identity})
    console.log(props.profile.identity)

    filter.get((err, results) => {
      if (!err) {
        const delHashes = Object.assign({}, this.hashes)
        for (let i in results) {
          if (delHashes[results[i].hash]) {
            delete delHashes[results[i].hash]
          }
        }

        if (Object.keys(delHashes).length > 0) {
          hashes = ''
          for (let h in this.hashes) {
            if (delHashes[h]) {
              delete this.hashes[h]
            } else {
              hashes += h
            }
          }
          localStorage.setItem(ORGANIZATION_APPROVAL_RECV_HASHES + this.props.device, hashes)
        }

        filter.watch((err, result) => {
          if (!err) {
            if (!this.hashes[result.hash]) {
              const json = new Buffer(result.payload.substring(2), 'hex').toString()
              console.log('recv {approval: %s}', json)

              const approval = JSON.parse(json)
              approval.resume.acceptTime = moment().unix()

              this.postApprovalAcceptance(props, approval, (err, result2) => {
                if (!err) {
                  this.state.approvals.push(approval)
                  this.setState({approvals: this.state.approvals})

                  let size = localStorage.getItem(ORGANIZATION_APPROVALS_KEY + this.props.device + '.size')
                  if (size) {
                    size = Number(size)
                  } else {
                    size = 0
                  }
                  localStorage.setItem(ORGANIZATION_APPROVALS_KEY + this.props.device + '.size', size + 1)
                  localStorage.setItem(ORGANIZATION_APPROVALS_KEY + this.props.device + '.' + size,
                    JSON.stringify(approval))

                  this.hashes[result.hash] = true
                  hashes += result.hash
                  localStorage.setItem(ORGANIZATION_APPROVAL_RECV_HASHES + this.props.device, hashes)
                }
              })
            }
          } else {
            console.error(err)
          }
        })
      } else {
        console.error(err)
      }
    })
  }

  componentWillReceiveProps(nextProps) {
    if (!this.preprocessed && nextProps.profile) {
      this.watchApprovalRequest(nextProps)
      this.preprocessed = true
    }
  }

  postResponse(from, to, payload, callback) {
    try {
      const web3 = this.props.web3
      const shh = web3.shh
      shh.post({
        from: from,
        to: to,
        topics: [this.props.topicName],
        payload: new Buffer(JSON.stringify(payload)).toString('hex'),
        ttl: TOPIC_MSG_TTL
      }, (err, result) => {
        if (!err) {
          console.log('shh post {to: %s}', to)
        } else {
          console.error(err)
        }
        callback(err, result)
      })
    } catch (err) {
      console.error(err)
      callback(err, null)
    }
  }

  postApprovalAcceptance(props, approval, callback) {
    console.log('post acceptance {id: %s}', approval.resume.id)
    const payload = {id: approval.resume.id, acceptTime: approval.resume.acceptTime}
    this.postResponse(props.profile.identity, approval.profile.identity, payload, callback)
  }

  postApprovalResponse(approval, callback) {
    console.log('post approval response {id: %s, result: %s}', approval.resume.id, approval.resume.approveResult)
    const payload = {
      id: approval.resume.id, approveResult: approval.resume.approveResult, approveTime: approval.resume.approveTime,
    }
    this.postResponse(this.props.profile.identity, approval.profile.identity, payload, callback)
  }

  approveClick(index, approveResult) {
    this.state.approving[index] = true
    this.setState({approving: this.state.approving})

    const approval = this.state.approvals[index]

    const web3 = this.props.web3

    const hash = web3.sha3(approval.profile.identity + approval.resume.id)

    try {
      unlockAccount(this.props.web3.personal, this.props.lockedTime, this.props.account, this.props.password,
        (result, unlockTime) => {
          if (result) {
            if (unlockTime) {
              this.props.onAutoUnlocked(unlockTime)
            }

            const abi = JSON.parse(this.props.abi)

            const eth = web3.eth
            const instance = eth.contract(abi[ORGANIZATION_CONTRACT_NAME]).at(this.props.agent)

            const gas = instance.approve.estimateGas(hash, {from: this.props.account})
            console.log('approve {gas: %d}', gas)

            const transactionHash = instance.approve(hash, {from: this.props.account, gas: gas})
            console.log('approve {transactionHash: %s}', transactionHash)

            watchTransaction(eth, transactionHash, (err, receipt) => {
              if (!err) {
                console.log(receipt)

                approval.resume.approveResult = approveResult
                approval.resume.approveTime = moment().unix()

                this.postApprovalResponse(approval, (err, result) => {
                  if (!err) {
                    delete this.state.approving[index]
                    this.setState({approving: this.state.approving, approvals: this.state.approvals})
                  } else {
                    delete this.state.approving[index]
                    this.setState({approving: this.state.approving})
                  }
                })

                this.props.onApproved(approval, approveResult)
              } else {
                console.error(err)
                delete this.state.approving[index]
                this.setState({approving: this.state.approving})
              }
            })
          } else {
            console.error(err)
            delete this.state.approving[index]
            this.setState({approving: this.state.approving})
          }
        })
    } catch (err) {
      console.error(err)
      delete this.state.approving[index]
      this.setState({approving: this.state.approving})
    }
  }

  formatMonth(time) {
    if (time > 0) {
      return moment.unix(time).format('YYYY/MM')
    }
    return ''
  }

  formatTime(time) {
    if (time > 0) {
      return moment.unix(time).format('YYYY/MM/DD HH:mm:ss')
    }
    return ''
  }

  render() {
    const rows = []
    for (let i in this.state.approvals) {
      const approval = this.state.approvals[i]

      rows.push(<tr key={i}>
        <td>Name: {approval.profile.name}<br/>Birthday: {approval.profile.birthday}</td>
        <td>{this.formatMonth(approval.resume.from)}</td>
        <td>{this.formatMonth(approval.resume.to)}</td>
        <td>{(approval.resume.approveTime > 0) ? (approval.resume.approveResult)
          ? <div><Label bsStyle="primary">Approved</Label>&nbsp;{this.formatTime(approval.resume.approveTime)}</div>
          : <div><Label bsStyle="danger">Disapproved</Label>&nbsp;{this.formatTime(approval.resume.approveTime)}</div>
          : (approval.resume.acceptTime > 0) ? <div>
          <Button bsStyle="info" bsSize="small" onClick={this.approveClick.bind(this, i, true)}
                  disabled={this.props.password.length == 0 || this.props.balance.toNumber() == 0
                  || this.state.approving[i]} style={{width: 100}}>Approve</Button>&nbsp;
          <Button bsStyle="danger" bsSize="small" onClick={this.approveClick.bind(this, i, false)}
                  disabled={this.props.password.length == 0 || this.props.balance.toNumber() == 0
                  || this.state.approving[i]}>Disapprove</Button>
          <br/>&nbsp;<Label bsStyle="warning">Accepted</Label>&nbsp;{this.formatTime(approval.resume.acceptTime)}</div>
          : <Label bsStyle="default">Accepting</Label>}</td>
      </tr>)
    }

    return <Table striped bordered condensed hover>
      <thead>
      <tr>
        <th>Job applicant</th>
        <th>From</th>
        <th>To</th>
        <th></th>
      </tr>
      </thead>
      <tbody>
      {rows}
      </tbody>
    </Table>
  }
}
