export class EthUtils {

  constructor(web3) {
    this.web3 = web3
  }

  unlockAccount(account, password, nextLockTime, callback) {
    if (new Date().getTime() >= nextLockTime) {
      this.web3.personal.unlockAccount(account, password, ACCOUNT_UNLOCK_DURATION + 10, (err, result) => {
        if (!err && result) {
          callback(true)
        } else {
          this.error = err
          console.error(err)
          callback(false)
        }
      })
    } else {
      this.error = err
      console.error(err)
      callback(true)
    }
  }

  getLastError() {
    return this.error
  }
}

export function unlockAccount(personal, lockedTime, account, password, callback) {
  const unlockTime = new Date().getTime()
  if (unlockTime >= lockedTime) {
    personal.unlockAccount(account, password, ACCOUNT_UNLOCK_DURATION + 10, (err, result) => {
      if (!err) {
        if (result) {
          console.log('unlocked {account: %s}', account)
          callback(true, unlockTime)
        }
      } else {
        console.error(err)
        callback(false)
      }
    })
  } else {
    callback(true)
  }
}

export function watchTransaction(eth, transactionHash, callback) {
  const filter = eth.filter('latest').watch((err, blockHash) => {
    if (!err) {
      console.log('watch block {blockHash: %s}', blockHash)
      eth.getBlock(blockHash, (e, block) => {
        for (let i in block.transactions) {
          console.log('found transaction {transactionHash: %s}', block.transactions[i])
          if (block.transactions[i] == transactionHash) {
            filter.stopWatching()

            const transaction = eth.getTransaction(transactionHash)
            console.log(transaction)

            eth.getTransactionReceipt(transactionHash, callback)
          }
        }
      })
    } else {
      console.error(err)
      callback(err, null)
    }
  })
}
