import React, {Component, PropTypes} from 'react'
import {render} from 'react-dom'
import Web3 from 'web3'

import {GETH_RPC} from './configure'
import System from './system/System.jsx'
import System2 from './system/System2.jsx'
import Organization from './organization/Organization.jsx'
import Organization2 from './organization/Organization2.jsx'
import Organization3 from './organization/Organization3.jsx'
import Applicant from './applicant/Applicant.jsx'
import Applicant2 from './applicant/Applicant2.jsx'

import {APPROVAL_TOPIC_NAME, PERMISSION_TOPIC_NAME, APPROVAL_TOPIC2_NAME, PERMISSION_TOPIC2_NAME} from './configure'

class DApps extends Component {

  constructor(props) {
    super(props);

    this.web3 = new Web3()
    this.web3.setProvider(new this.web3.providers.HttpProvider(GETH_RPC))

    const match = location.search.match(/[\?|\&]device=(.*)$/)
    if (match) {
      this.device = match[1]
    } else {
      this.device = ''
    }
  }

  render() {
    const className = document.getElementById('app').className
    if (className == 'system') {
      return <System web3={this.web3}/>
    } else if (className == 'system2') {
      return <System2 web3={this.web3}/>
    } else if (className == 'organization') {
      return <Organization web3={this.web3} device={this.device}/>
    } else if (className == 'organization2') {
      return <Organization2 web3={this.web3} device={this.device}/>
    } else if (className == 'organization3') {
      return <Organization3 web3={this.web3} device={this.device}/>
    } else if (className == 'applicant') {
      return <Applicant web3={this.web3} device={this.device}/>
    } else if (className == 'applicant2') {
      return <Applicant2 web3={this.web3} device={this.device} aTopicName={APPROVAL_TOPIC_NAME}
                         pTopicName={PERMISSION_TOPIC_NAME}/>
    } else if (className == 'applicant3') {
      return <Applicant2 web3={this.web3} device={this.device} aTopicName={APPROVAL_TOPIC2_NAME}
                         pTopicName={PERMISSION_TOPIC2_NAME}/>
    }
  }
}

render(<DApps/>, document.getElementById('app'))
