import React, {Component, PropTypes} from 'react'
import {Button, Table} from 'react-bootstrap'
import moment from 'moment'

import {SYSTEM_CONTRACT_NAME} from '../configure'
import {unlockAccount, watchTransaction} from '../utils'

export default class Applicants extends Component {

  static propTypes = {
    web3: PropTypes.object.isRequired,
    account: PropTypes.string.isRequired,
    password: PropTypes.string.isRequired,
    lockedTime: PropTypes.number.isRequired,
    balance: PropTypes.object.isRequired,
    abi: PropTypes.string.isRequired,
    agent: PropTypes.string.isRequired,
    onStopped: PropTypes.func.isRequired,
    onAutoUnlocked: PropTypes.func.isRequired
  }

  state = {
    applicants: [],
    stopping: {}
  }

  loadApplicants(props) {
    const abi = JSON.parse(props.abi)

    const eth = this.props.web3.eth
    const instance = eth.contract(abi[SYSTEM_CONTRACT_NAME]).at(props.agent)

    const size = instance.sizeApplicants({from: props.account}).toNumber()
    const applicants = []
    for (let i = 0; i < size; i++) {
      const applicant = instance.getApplicant(i, {from: props.account})
      applicants.push({account: applicant[0], agent: applicant[1], createTime: applicant[2].toNumber()})
    }
    return applicants
  }

  watchUpdate(props) {
    console.log('watch ApplicantsUpdate')

    const abi = JSON.parse(props.abi)

    const eth = this.props.web3.eth
    const instance = eth.contract(abi[SYSTEM_CONTRACT_NAME]).at(props.agent)

    instance.ApplicantsUpdate().watch((err, event) => {
      if (!err) {
        console.log('ApplicantsUpdate event {sender: %s}', event.args.sender)

        const applicants = this.loadApplicants(props)
        if (applicants) {
          this.setState({applicants: applicants})
        }
      } else {
        console.error(err)
      }
    })
  }

  componentWillReceiveProps(nextProps) {
    if (!this.loaded && nextProps.account.length > 0 && nextProps.abi.length > 0 && nextProps.agent.length > 0) {
      const applicants = this.loadApplicants(nextProps)
      if (applicants) {
        this.setState({applicants: applicants})
      }
      this.watchUpdate(nextProps)

      this.loaded = true
    }
  }

  stopAgentClick(agent) {
    this.state.stopping[agent] = true
    this.setState({stopping: this.state.stopping})

    try {
      unlockAccount(this.props.web3.personal, this.props.lockedTime, this.props.account, this.props.password,
        (result, unlockTime) => {
          if (result) {
            if (unlockTime) {
              this.props.onAutoUnlocked(unlockTime)
            }

            const abi = JSON.parse(this.props.abi)

            const eth = this.props.web3.eth
            const instance = eth.contract(abi[SYSTEM_CONTRACT_NAME]).at(this.props.agent)

            const gas = instance.removeApplicant.estimateGas(agent, {from: this.props.account})
            console.log('removeApplicant {gas: %d}', gas)

            const transactionHash = instance.removeApplicant(agent, {from: this.props.account, gas: gas})
            console.log('removeApplicant {transactionHash; %s}', transactionHash)

            watchTransaction(eth, transactionHash, (err, receipt) => {
              if (!err) {
                console.log(receipt)

                this.props.onStopped(agent)
                for (let i in this.state.applicants) {
                  if (this.state.applicants[i].agent == agent) {
                    this.state.applicants.splice(i, 1);
                    break;
                  }
                }
                delete this.state.stopping[agent]
                this.setState({stopping: this.state.stopping, applicants: this.state.applicants})
              } else {
                console.error(err)
                delete this.state.stopping[agent]
                this.setState({stopping: this.state.stopping})
              }
            })
          } else {
            delete this.state.stopping[agent]
            this.setState({stopping: this.state.stopping})
          }
        })
    } catch (err) {
      console.error(err)
      delete this.state.stopping[agent]
      this.setState({stopping: this.state.stopping})
    }
  }

  formatTime(time) {
    if (time > 0) {
      return moment.unix(time).format('YYYY/MM/DD HH:mm:ss')
    }
    return ''
  }

  render() {
    const rows = []
    for (let i in this.state.applicants) {
      const applicant = this.state.applicants[i]
      rows.push(<tr key={i}>
        <td style={{width: 200}}>
          <div style={{width: 200, overflowX: 'scroll'}}>{applicant.agent}</div>
        </td>
        <td style={{width: 200}}>
          <div style={{width: 200, overflowX: 'scroll'}}>{applicant.account}</div>
        </td>
        <td>{this.formatTime(applicant.createTime)}</td>
        <td><Button bsStyle="danger" bsSize="small" onClick={this.stopAgentClick.bind(this, applicant.agent)}
                    disabled={this.props.password.length == 0 || this.props.balance.toNumber() == 0
                    || this.state.stopping[applicant.agent]}
        >{(this.state.stopping[applicant.agent]) ? 'Stopping ...' : 'Stop agent'}</Button></td>
      </tr>)
    }

    return <Table striped bordered condensed hover>
      <thead>
      <tr>
        <th>Agent</th>
        <th>Account</th>
        <th>Create time</th>
        <th></th>
      </tr>
      </thead>
      <tbody>
      {rows}
      </tbody>
    </Table>
  }
}
