import React, {Component, PropTypes} from 'react'
import {Button, Table, Label} from 'react-bootstrap'
import moment from 'moment'

import {ORGANIZATION_CONTRACT_NAME, ORGANIZATION_PERMISSIONS_KEY, ORGANIZATION_PERMISSION_RECV_HASHES}
  from '../configure'

import Alert from '../Alert.jsx'

export default class Permissions2 extends Component {

  static propTypes = {
    web3: PropTypes.object.isRequired,
    device: PropTypes.string,
    profile: PropTypes.shape({
      identity: React.PropTypes.string,
      name: React.PropTypes.string,
      createTime: React.PropTypes.number,
      updateTime: React.PropTypes.number
    }),
    abi: PropTypes.string.isRequired,
    topicName: PropTypes.string.isRequired
  }

  static defaultProps = {
    device: '0'
  }

  state = {
    permissions: [],
    permission: null,
    accessing: {},
    message: null
  }

  loadPermissions() {
    const permissions = []
    let size = localStorage.getItem(ORGANIZATION_PERMISSIONS_KEY + this.props.device + '.size')
    if (size) {
      size = Number(size)
      for (let i = 0; i < size; i++) {
        permissions.push(JSON.parse(localStorage.getItem(ORGANIZATION_PERMISSIONS_KEY + this.props.device + '.' + i)))
      }
    }
    return permissions
  }

  componentDidMount() {
    this.setState({permissions: this.loadPermissions()})
  }

  watchPermission(props) {
    console.log('watch permission')

    let hashes = localStorage.getItem(ORGANIZATION_PERMISSION_RECV_HASHES + this.props.device)
    if (hashes) {
      this.hashes = {}
      const size = hashes.length / 66
      for (let i = 0; i < size; i++) {
        const hash = hashes.substring(i * 66, (i + 1) * 66)
        this.hashes[hash] = true
      }
    } else {
      hashes = ''
      this.hashes = {}
    }

    const web3 = this.props.web3
    const shh = web3.shh
    const filter = shh.filter({topics: [this.props.topicName], to: props.profile.identity})

    filter.get((err, results) => {
      if (!err) {
        const delHashes = Object.assign({}, this.hashes)
        for (let i in results) {
          if (delHashes[results[i].hash]) {
            delete delHashes[results[i].hash]
          }
        }

        if (Object.keys(delHashes).length > 0) {
          hashes = ''
          for (let h in this.hashes) {
            if (delHashes[h]) {
              delete this.hashes[h]
            } else {
              hashes += h
            }
          }
          localStorage.setItem(ORGANIZATION_PERMISSION_RECV_HASHES + this.props.device, hashes)
        }

        filter.watch((err, result) => {
          if (!err) {
            if (!this.hashes[result.hash]) {
              const json = new Buffer(result.payload.substring(2), 'hex').toString()
              console.log('recv {permission: %s}', json)

              const permission = JSON.parse(json)

              this.state.permissions.push(permission)
              this.setState({permissions: this.state.permissions})

              let size = localStorage.getItem(ORGANIZATION_PERMISSIONS_KEY + this.props.device + '.size')
              if (size) {
                size = Number(size)
              } else {
                size = 0
              }
              localStorage.setItem(ORGANIZATION_PERMISSIONS_KEY + this.props.device + '.size', size + 1)
              localStorage.setItem(ORGANIZATION_PERMISSIONS_KEY + this.props.device + '.' + size,
                JSON.stringify(permission))

              this.hashes[result.hash] = true
              hashes += result.hash
              localStorage.setItem(ORGANIZATION_PERMISSION_RECV_HASHES + this.props.device, hashes)
            }
          } else {
            console.error(err)
          }
        })
      } else {
        console.error(err)
      }
    })
  }

  componentWillReceiveProps(nextProps) {
    if (!this.preprocessed && nextProps.profile) {
      this.watchPermission(nextProps)
      this.preprocessed = true
    }
  }

  accessResumesClick(index) {
    const permission = this.state.permissions[index]

    const now = moment().unix()
    if (permission.from > now || permission.to < now) {
      this.setState({message: ''})
      return
    }

    this.state.accessing[index] = true
    this.setState({accessing: this.state.accessing})

    if (!permission.confirmed) {
      const abi = JSON.parse(this.props.abi)

      const web3 = this.props.web3
      const eth = web3.eth

      for (let i in permission.resumes) {
        const hash = web3.sha3(permission.profile.identity + permission.resumes[i].id)

        const instance = eth.contract(abi[ORGANIZATION_CONTRACT_NAME]).at(permission.resumes[i].orgAgent)
        try {
          if (!instance.existsApproval(hash)) {
            permission.resumes[i].approveResult = false
          }
        } catch (err) {
          console.error(err)
        }
      }
      permission.confirmed = true

      this.state.accessing[index] = false
      this.setState({accessing: this.state.accessing, permissions: this.state.permissions, permission: permission})
      localStorage.setItem(ORGANIZATION_PERMISSIONS_KEY + this.props.device + '.' + index, JSON.stringify(permission))
    } else {
      this.state.accessing[index] = false
      this.setState({accessing: this.state.accessing, permission: permission})
    }
  }

  formatMonth(time) {
    if (time > 0) {
      return moment.unix(time).format('YYYY/MM')
    }
    return ''
  }

  formatDay(time) {
    if (time > 0) {
      return moment.unix(time).format('YYYY/MM/DD')
    }
    return ''
  }

  formatTime(time) {
    if (time > 0) {
      return moment.unix(time).format('YYYY/MM/DD HH:mm:ss')
    }
    return ''
  }

  render() {
    const now = moment().unix()

    const rows = []
    for (let i in this.state.permissions) {
      const permission = this.state.permissions[i]
      rows.push(<tr key={i}>
        <td>Name: {permission.profile.name}<br/>Birthday: {permission.profile.birthday}</td>
        <td>{this.formatDay(permission.from)}</td>
        <td>{this.formatDay(permission.to)}</td>
        <td>
          <Button bsStyle="info" bsSize="small" onClick={this.accessResumesClick.bind(this, i)}
                  disabled={permission.from > now || permission.to < now || !permission.profile}>Access</Button>
        </td>
      </tr>)
    }

    let resumes
    if (this.state.permission) {
      const resumesRows = []
      for (let i in this.state.permission.resumes) {
        const resume = this.state.permission.resumes[i]
        if (resume.approveResult) {

        }
        resumesRows.push(<tr key={i}>
          <td>{resume.name}</td>
          <td>{this.formatMonth(resume.from)}</td>
          <td>{this.formatMonth(resume.to)}</td>
          <td>{(resume.approveResult)
            ? <div><Label bsStyle="primary">Approved</Label></div>
            : <div><Label bsStyle="danger">Disapproved</Label></div>
          }</td>
        </tr>)
      }

      resumes = <div>
        <hr/>
        <h5>{this.state.permission.profile.name} resume</h5>
        <Table striped bordered condensed hover>
          <thead>
          <tr>
            <th>Company / School</th>
            <th>From</th>
            <th>To</th>
            <th>Status</th>
          </tr>
          </thead>
          <tbody>
          {resumesRows}
          </tbody>
        </Table>
      </div>
    }

    return <div>
      <Table striped bordered condensed hover>
        <thead>
        <tr>
          <th>Job applicant</th>
          <th>From</th>
          <th>End</th>
          <th></th>
        </tr>
        </thead>
        <tbody>
        {rows}
        </tbody>
      </Table>
      {resumes}
      <Alert message={this.state.message} onHide={() => this.setState({message: null})}/>
    </div>
  }
}
